// hide-show classes

.hide {
  display: none; }

.show-only-small {
  display: none;

  @media only screen and (max-width: $break-small-mid) {
    display: block; } }

.hide-only-small {
  display: block;

  @media only screen and (max-width: $break-small-mid) {
    display: none; } }

.show-only-large {
  display: none;

  @media only screen and (min-width: $break-mid-large) {
    display: block; } }

.hide-only-large {
  display: block;

  @media only screen and (min-width: $break-mid-large) {
    display: none; } }

